            你好 <span v-if="userBody.name">,</span>{{ userBody.name }}
          </div>
          <div class="head-t-all-l-text1">工商管理学院｜工商管理</div>
        </div>
        <div class="head-t-all-r">
          <img :src="userBody.photo" alt="" v-if="userBody.photo" />
          <img src="@/assets/mobile-boy@3x.png" alt="" v-else />
        </div>
      </div>
    </div>

    <div class="home-lists" v-if="list.length != 0">
      <div
        v-for="(item, index) in list"
        :key="index"
        class="home-lists-li"
        @click="goSub(item.title, item.href)"
