    </div> -->

    <HomeTop :i="checkIndex" />
  </div>
</template>

<script>
import HomeTop from "@/components/homeTop.vue";
export default {
  name: "Mine",
  components: {
    HomeTop,
  },
  inject: ["reload"],
  data() {
    return {
      checkIndex: 4,
      list: [],
      bgc: this.GLOBAL.bgc,
      userBody: {},
    };
  },
  methods: {
    goSub(title, href) {
      console.log(title, href);
      // if (title == "双选会") {
      //   this.$router.push({ name: "shuangMore" });
      // } else if (title == "招聘信息") {
      //   this.$router.push({ name: "zhaoMore" });
      //   this.$store.commit("updataTypeText", 0);
      // } else if (title == "宣讲会") {
      //   this.$router.push({ name: "xuanMore" });
      // } else {
      // }

      // window.location.href = href;
      this.$router.push({ name: "activityMore" });
    },
    getList() {
      const params = {
        type: "mobile",
        num: 6,
      };
      console.log(params);
      this.$axios
        .get("f/ajaxHome/ajax_quickEntryList?type=mobile" + "&num=" + 6)
        .then((res) => {
          console.log(res, "中部");
          if (res.data.state == 1) {
            if (res.data.data) {
              this.list = res.data.data;
            }
          } else {
            this.list = [];
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
    getUser() {
      this.$axios
        .get("f/ajaxHome/ajax_getUser")
        .then((res) => {
          console.log(res, "获取用户");
          if (res.data.state == 1) {
            this.userBody = res.data.object;
          } else {
            this.userBody = {};
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
  },
  created() {
    // 获取导航栏
    // this.getList();
    // 获取用户
    // this.getUser();

    window.location.href = "/a/common/student/studentinfo/my";
  },
  beforeDestory() {},
  destroyed() {},
};
</script>
<style lang="css" scoped>
.con {
  background-color: #f5f5f5;
  padding-bottom: 1.66rem;
  min-height: 100%;
}
.head-t {
  width: 7.5rem;
  height: 4rem;
  background-image: linear-gradient(
    135deg,
    #5993ff 0%,
    #4760d7 42%,
    #5c42de 100%
  );
  padding: 0.44rem 0.4rem 0 0.4rem;
  box-sizing: border-box;
}
.head-t img {
  width: 100%;
  height: 100%;
}
.head-t-all {
  width: 6.7rem;
  height: 1.24rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.head-t-all-l {
  height: 1.2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.head-t-all-l-text {
  font-family: PingFangSC-Regular;
  font-size: 0.44rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rpx;
  color: #ffffff;
  text-align: left;
}
.head-t-all-l-text1 {
  font-family: PingFangSC-Medium;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rpx;
  color: #ffffff;
  text-align: left;
}
.head-t-all-r {
  width: 1.2rem;
  height: 1.2rem;
}
.head-t-all-r img {
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
}
.home-lists {
  width: 7.5rem;
  background-color: #ffffff;
  margin-top: -0.92rem;
  border-top-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
  padding: 0.52rem 0.44rem 0rem 0.44rem;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.home-lists-li {
  margin-bottom: 0.44rem;
  margin-right: 0.8rem;
}
.home-lists-li:nth-child(4n) {
  margin-right: 0rem !important;
}
.home-lists-li img {
  width: 0.88rem;
  height: 0.88rem;
}
.home-lists-li-text {
  font-family: PingFangSC-Regular;
  font-size: 0.26rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.36rem;
  letter-spacing: 0rem;
  color: #3f4967;
}
</style>


